.button{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    height: 50px;
    background-color: Grey;
    margin: 10px;
    cursor: pointer;
    user-select: none;
    text-decoration: none;
    color: white;
    font-size: 20px;
    border-radius: 10px;
}

.button:active{
    opacity: 0.5;
}