*{
    font-family: Roboto sans-serif;
}

.heading{
    display: flex;
    height: 200px;
    justify-content: center;
    align-items: center;
    margin: 30px 10%;
    padding: 10px 0 30px 0;
    border-radius: 15px;
}

.name{
    font-size: 40px;
    margin: 30px;
}


@media only screen and (max-width: 750px) {
    .name{
        font-size: 30px;
    }
}

.buttons{
    display: flex;
    justify-content: center;
}

