.bodyFrame{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    margin: 0 20%;
    font-family: Roboto sans-serif;
    font-size: 16px;
}

@media only screen and (max-width: 1000px) {
    .bodyFrame{
        margin: 0 10%;
    }
}

@media only screen and (max-width: 700px) {
    .bodyFrame{
        margin: 0 5%;
    }
}



img {
    width: 100%;
    margin: 10px 0;
}

.whiteBack{
    border: 2px solid black;
}

span{
    padding: 5px;
    background-color: lightgray;
}

.section{
    width: 100%;
}

.list{
    display: flex;
    flex-direction: column;
    width: 100%;
}

.list p{
    margin-bottom: 0;
    margin-left: 15px;
}

